html,
body {
  height: 100%;
}
body {
  height: auto !important;
  min-height: 100%;
  background: rgb(2, 0, 36);
  background: linear-gradient(190deg, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 35%, rgba(0, 212, 255, 1) 100%) fixed no-repeat;
  color: rgba(255, 255, 255, 0.829);
  padding: 20px;
  scroll-behavior: smooth;

  header {
    text-align: center;
    min-height: 100vh;
    position: relative;

    .container {
      width: 100%;
      text-align: center;
      position: absolute;
      top: 50%;
      transform: translateY(-80%);
      text-shadow: 1px 1px 2px black;
    }
  }

  article {
    min-height: 50vh;
    text-shadow: 1px 1px 2px black;
    h2 {
      text-align: center;
    }
    p {
      max-width: 600px;
      margin: 0 auto;
    }
  }

  #about {
    p {
      line-height: 30px;
    }
  }

  #projects {
    display: grid;
    grid-template-columns: repeat(2, 49%);
    grid-template-rows: repeat(4, auto);
    grid-gap: 15px 2%;
    h2 {
      grid-column: 1/3;
      grid-row: 1;
    }

    div[class^='project'] {
      height: 400px;
      background-color: red;
      padding: 20px;

      img {
        width: 100%;
        height: auto;
        max-width: 660px;
      }
    }
    p {
      font-size: 2em;
    }
  }

  footer {
    margin-top: 20px;
    background: rgba(255, 255, 250, 0.9);
    text-align: center;
    padding: 15px 0;
    a {
      margin-right: 10px;
      display: inline-block;
      line-height: 0;
      img {
        max-width: 70px;
      }
      &:last-child {
        margin-right: 0;
      }
    }
    p {
      margin-bottom: 0;
      color: #414040;
      font-size: 20px;
      font-weight: bold;
    }
  }
}
